import React from "react";
import { useFormik } from "formik";
import Modal from "react-bootstrap/Modal";
import InputField from "../product/InputField";
import { useDispatch, useSelector } from "react-redux";
import { Row } from "react-bootstrap";
import {
  GetPersonsById,
  Update_Persons,
} from "../../store/person/actions/actionsCreators";
import { ManagerUpdateValuesSchemas } from "../../Schemas/Schemas";
const PlayerInfoEditModel = ({ show, onClose, setEditModel, setState }) => {
  const Dispatch = useDispatch();
  const { PersonDetails } = useSelector((state) => state.person);
  console.log(PersonDetails?.data.playerResponse.gamesPlayed);
  const { user } = useSelector((state) => state.user);
  const userId = user.userId;
  const Token = user?.access_token;
  const initialValues = {
    email: PersonDetails.data.email,
    middleName: PersonDetails?.data.middleName || "",
    firstName: PersonDetails?.data.firstName || "",
    lastName: PersonDetails?.data.lastName || "",
    address1: PersonDetails?.data.address1 || "",
    address2: PersonDetails?.data.address2 || "",
    ranking: PersonDetails?.data?.playerResponse?.ranking || "",
    points: PersonDetails?.data?.playerResponse?.points || "",
    gamesPlayed: PersonDetails?.data?.playerResponse?.gamesPlayed || "",
    tournamentsPlayed:
      PersonDetails?.data?.playerResponse?.tournamentsPlayed || "",
    city: PersonDetails?.data?.city || "",
    state: PersonDetails?.data?.state || "",
    mobilePhone: PersonDetails?.data?.mobilePhone || "",
    zipCode: PersonDetails?.data?.zipCode || "",
    country: PersonDetails?.data?.country || "",
  };
  const { values, handleChange, errors, handleSubmit, touched } = useFormik({
    initialValues: initialValues,
    validationSchema: ManagerUpdateValuesSchemas,
    onSubmit: (values, action) => {
      const data = {
        middleName: values.middleName,
        email: PersonDetails.data.email,
        Password: null,
        role: "PLAYER",
        firstName: values.firstName,
        lastName: values.lastName,
        address1: values.address1,
        address2: values.address2,
        points: values.points,
        ranking: values.ranking,
        division: values.division,
        city: values.city,
        state: values.state,
        zipCode: values.zipCode,
        mobilePhone: values.mobilePhone,
        playerStatus: "ACTIVE",
        tournamentsPlayed: values.tournamentsPlayed,
        gamesPlayed: values.gamesPlayed,
        personAPlayer: true,
        country: values.country,
      };
      Dispatch(
        Update_Persons(data, userId, Token, () => {
          Dispatch(GetPersonsById(user.userId, Token));
          setEditModel(false);
        })
      );
      action.resetForm();
      setState((prev) => !prev);
    },
  });

  return (
    <Modal show={show} onHide={onClose} size="xl" centered className="py-4 ">
      <Modal.Header closeButton>
        <Modal.Title>Edit Info</Modal.Title>
      </Modal.Header>

      <form onSubmit={handleSubmit}>
        <Modal.Body>
          <Row className="row gy-3 row-cols-1 row-cols-sm-1 row-cols-md-2 row-cols-lg-2">
            <div className="d-flex flex-column flex-grow-1">
              <InputField
                type="email"
                name="email"
                label="Email"
                onChange={handleChange}
                value={values.email}
                className="form-control"
                touched={touched.email}
                error={errors.email}
                disabled
              />
            </div>

            {/* Name Field */}
            <div className="d-flex flex-column flex-grow-1">
              <InputField
                type="text"
                name="middleName"
                label="Middle Name"
                onChange={handleChange}
                value={values.middleName}
                className="form-control"
                touched={touched.middleName}
                error={errors.middleName}
              />
            </div>

            <div className="d-flex flex-column flex-grow-1">
              <InputField
                type="text"
                name="firstName"
                label="First Name"
                onChange={handleChange}
                value={values.firstName}
                className="form-control"
                touched={touched.firstName}
                error={errors.firstName}
              />
            </div>

            <div className="d-flex flex-column flex-grow-1">
              <InputField
                type="text"
                name="lastName"
                label="Last Name"
                onChange={handleChange}
                value={values.lastName}
                className="form-control"
                touched={touched.lastName}
                error={errors.lastName}
              />
            </div>

            <div className="d-flex flex-column flex-grow-1">
              <InputField
                type="text"
                name="address1"
                label="Address 1"
                onChange={handleChange}
                value={values.address1}
                className="form-control"
                touched={touched.address1}
                error={errors.address1}
              />
            </div>

            <div className="d-flex flex-column flex-grow-1">
              <InputField
                type="text"
                name="address2"
                label="Address 2"
                onChange={handleChange}
                value={values.address2}
                className="form-control"
                touched={touched.address2}
                error={errors.address2}
              />
            </div>

            <div className="d-flex flex-column flex-grow-1">
              <InputField
                type="text"
                name="city"
                label="City"
                onChange={handleChange}
                value={values.city}
                className="form-control"
                touched={touched.city}
                error={errors.city}
              />
            </div>

            <div className="d-flex flex-column flex-grow-1">
              <InputField
                type="text"
                name="state"
                label="State"
                onChange={handleChange}
                value={values.state}
                className="form-control"
                touched={touched.state}
                error={errors.state}
              />
            </div>

            <div className="d-flex flex-column flex-grow-1">
              <InputField
                type="text"
                name="zipCode"
                label="Zip Code"
                onChange={handleChange}
                value={values.zipCode}
                className="form-control"
                touched={touched.zipCode}
                error={errors.zipCode}
              />
            </div>

            <div className="d-flex flex-column flex-grow-1">
              <InputField
                type="number"
                name="ranking"
                label="Ranking"
                onChange={handleChange}
                value={values.ranking}
                className="form-control"
                touched={touched.ranking}
                error={errors.ranking}
              />
            </div>

            <div className="d-flex flex-column flex-grow-1">
              <InputField
                type="text"
                name="mobilePhone"
                label="Mobile Phone"
                onChange={handleChange}
                value={values.mobilePhone}
                className="form-control"
                touched={touched.mobilePhone}
                error={errors.mobilePhone}
              />
            </div>
            <div className="d-flex flex-column flex-grow-1">
              <InputField
                type="text"
                name="country"
                label="Country"
                onChange={handleChange}
                value={values.country}
                className="form-control"
                touched={touched.country}
                error={errors.country}
              />
            </div>
            <div className="d-flex flex-column flex-grow-1">
              <InputField
                type="number"
                name="tournamentsPlayed"
                label="Tournaments Played"
                onChange={handleChange}
                value={values.tournamentsPlayed}
                className="form-control"
                touched={touched.tournamentsPlayed}
                error={errors.tournamentsPlayed}
              />
            </div>
            <div className="d-flex flex-column flex-grow-1">
              <InputField
                type="number"
                name="points"
                label="Points"
                onChange={handleChange}
                value={values.points}
                className="form-control"
                touched={touched.points}
                error={errors.points}
              />
            </div>

            {/* Ranking Field */}
            <div className="d-flex flex-column flex-grow-1">
              <InputField
                type="number"
                name="ranking"
                label="Ranking"
                onChange={handleChange}
                value={values.ranking}
                className="form-control"
                touched={touched.ranking}
                error={errors.ranking}
              />
            </div>
            {/* Games Played Field */}
            <div className="d-flex flex-column flex-grow-1">
              <InputField
                type="number"
                name="gamesPlayed"
                label="Games Played"
                onChange={handleChange}
                value={values.gamesPlayed}
                className="form-control"
                touched={touched.gamesPlayed}
                error={errors.gamesPlayed}
              />
            </div>
          </Row>
          <div className="d-flex ">
            <button type="submit" className="mt-3 gradient-btn-orange">
              Submit
            </button>
          </div>
        </Modal.Body>
      </form>
    </Modal>
  );
};

export default PlayerInfoEditModel;
